	onLoad(options) {
		var data = {};
		data = JSON.parse(options.id)
		this.objData = data
		this.money = data.totalPrice
		var formData = {
			payType: '002'
		}
		var objData = Object.assign(formData, this.objData)
		this.dataForm = objData
		setTimeout(() =>{
			this.checkOrder()
		}, 3000)
